@import url(https://fonts.google.com/specimen/Gugi?query=gugi);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.mainpage{width: 90%; height: 100vh; margin: auto; font-family: 'gugi' cursive;}

.mainpage h1{margin: 0; padding: 20px 0; text-align: center;}

.Cards_style img{width: 200px; height: 200px;}


